<template>
  <div class="home-container">

    <nav-bar></nav-bar>

    <div class="contents">

      <home-header></home-header>

      <div class="form-container">
        <input @keyup.enter="onSearchInput()" :value="input" v-model="input" type="text" class="form-control" id="searchInput" :placeholder="placeholder">
        <button @click="onSearchInput()" type="submit" class="btn btn-default">Search</button>

        <!--<div v-show="input" class="form-guide-container" v-if="sub_categories">-->
          <!--<div v-for="(category, index) in sub_categories" v-if="index < 7">-->
            <!--<div @click="onSearchGuide(category.sub_category)" class="category-result-container" v-if="category">-->
              <!--{{filterKeyword(category.sub_category)}}-->
            <!--</div>-->
          <!--</div>-->
          <!--<div v-if="sub_categories">-->
            <!--No result.-->
          <!--</div>-->
        <!--</div>-->

      </div>

      <h2 style="font-size:32px; font-weight: 700; margin-bottom: 0" class="title">Featured Suppliers</h2>

      <featured-card line="on" keywordData="7"></featured-card>
      <featured-card line="on" keywordData="10"></featured-card>
      <featured-card line="on" keywordData="5"></featured-card>
      <featured-card line="on" keywordData="3"></featured-card>
      <featured-card line="on" keywordData="4"></featured-card>
      <featured-card line="on" keywordData="1"></featured-card>
      <featured-card line="on" keywordData="11"></featured-card>
      <featured-card line="off" keywordData="6"></featured-card>

    </div>

    <footer-bar></footer-bar>

  </div>
</template>

<script>
  import NavBar from '../components/NavBar.vue'
  import HomeHeader from '../components/HomeHeader'
  import FeaturedCard from '../components/FeaturedCard'
  import FooterBar from '../components/FooterBar'

  export default {
    components: {
      NavBar,
      HomeHeader,
      FeaturedCard,
      FooterBar
    },
    data () {
      return {
        msg: {
          inputGuide: 'Loading..'
        },
        input: '',
        sub_categories: null,
        inputActive: false,
        placeholder: 'Try "Toy"'
      }
    },
    methods: {
      onSearchInput: function () {
        if (this.input) {
          this.$router.push({
            path: '/search',
            query: {
              input: this.input
            }
          })
        }
      },
      onSearchGuide (keyword) {
        this.$router.push({
          path: '/search',
          query: {
            input: keyword
          }
        })
      },
      filterKeyword (keyword) {
        if (keyword.indexOf(this.input) !== -1) {
          return keyword
        }
      },
      getCategory () {
        this.$http.get(`/api/data/category/sub`)
          .then(res => {
            this.sub_categories = res.data
          })
      },
      isInputFocus () {
        $(document).ready(function () {
          if ($('#searchInput').is(':focus')) {
            console.log('focus')
            this.inputActive = true
          } else {
            console.log('not focus')
            this.inputActive = false
          }
        })
      }
    },
    created () {
      console.log('Home created')
      this.getCategory()
    },
    mounted () {
      console.log('Home mounted')
    },
    updated () {
      console.log('Home updated')
      this.inputActive = true
    }
  }
</script>

<style scoped>
  /*Form container*/
  .form-container {
    position: relative;
    padding: 10px;
    border: 1px lightgray solid;
    border-radius: 4px;
    margin-bottom: 115px;
    box-shadow: 1px 1px 10px 1px #e4e4e4;
    height: 50px;
  }
  .form-container input {
    position: absolute;
    margin-right: 300px;
    height: 30px;
    font-size: 17px;
    font-weight: 400;
    border: none;
    box-shadow: none;
    outline: none;
    width: 220px;
  }
  .form-container button {
    display: none;
  }
  .form-container .form-guide-container {
    background-color: #FFF;
    position: absolute;
    z-index: 9998;
    font-size:16px;
    top: 100%;
    left: 0;
    border-left: 1px solid lightgrey;
    border-right: 1px solid lightgrey;
    border-bottom: 1px solid lightgrey;
    box-shadow: 1px 1px 10px lightgrey;
    width: 100%;
  }
  .form-guide-container .category-result-container {
    padding: 10px 6px;
  }
  .form-guide-container .category-result-container:hover {
    background-color: #eeeeee;
    cursor: pointer;
  }

  @media ( min-width: 768px ) {
    .form-container {
      margin-bottom: 175px;
    }
    .form-container input {
      width: 90%;
    }
  }
  @media ( min-width: 1024px ) {
    .form-container {
      position: relative;
      padding: 10px;
      border: 1px lightgray solid;
      border-radius: 4px;
      margin-bottom: 80px;
      box-shadow: 1px 1px 10px 1px #e4e4e4;
      height: 80px;
    }
    .form-container input {
      position: absolute;
      margin-right: 300px;
      height: 60px;
      font-size: 20px;
      font-weight: 400;
      border: none;
      box-shadow: none;
      outline: none;
      width: 900px;
    }
    .form-container button {
      position: absolute;
      font-weight: 500;
      right: 12px;
      font-size: 16px;
      height: 60px;
      width: 100px;
      background-color: #f2583d;
      color: #FFF;
      border: none;
      display: inherit;
    }
  }


</style>



