<template>
  <div class="page-container">

    <div class="form-contents">

      <auth-header></auth-header>

      <div class="form-container">
        <div class="row">

          <div class="col-md-12">
            <div class="email-container">
              <span>Email</span>
              <input v-model="email" :value="email" id="input-email" class="form-control" type="text" :placeholder="placeholder.email">
            </div>
          </div>

          <br>

          <div class="col-md-12">
            <div class="firstname-container">
              <span>First name</span>
              <input v-model="firstname" :value="firstname" id="input-first" class="form-control" type="text" :placeholder="placeholder.firstname">
            </div>
          </div>

          <div class="col-md-12">
            <div class="firstname-container">
              <span>Last name</span>
              <input v-model="lastname" :value="lastname" id="input-last" class="form-control" type="text" :placeholder="placeholder.lastname">
            </div>
          </div>

          <br>

          <div class="col-md-12">
            <div class="password-container">
              <span>Password</span>
              <input v-model="password" :value="password" id="input-password" class="form-control" type="password" :placeholder="placeholder.password">
            </div>
          </div>

          <br>

          <div class="col-md-12">
            <div class="button-container">
              <button id="button-signup" class="btn-lg btn-default" @click="onSignUpButton" @keyup.enter="onLoginButton">
                Sign up
              </button>
            </div>
            <hr>
          </div>

          <div class="col-md-12">
            <div class="signup-container">
              <a id="text-login" @click="onLoginButton">Did you already have account?</a>
              <button class="btn btn-default" id="button-login" @click="onLoginButton">
                Login
              </button>
            </div>
          </div>

        </div>
      </div>

    </div>

    <footer-bar></footer-bar>
  </div>
</template>

<script>
  import AuthHeader from '../components/AuthHeader'
  import FooterBar from '../components/FooterBar'

  export default {
    metaInfo: {
      title: 'Factory Hunt - Sign up'
    },
    components: {
      AuthHeader,
      FooterBar
    },
    data () {
      return {
        email: '',
        firstname: '',
        lastname: '',
        password: '',
        placeholder: {
          email: 'your@email.com',
          firstname: 'first name',
          lastname: 'last name',
          password: '*******'
        }
      }
    },
    methods: {
      onSignUpButton: function () {
        const data = {
          email: this.email,
          password: this.password,
          first_name: this.firstname,
          last_name: this.lastname
        }
        this.$http.post('/api/data/lead/upload', data)
          .then(res => {
            if (res.data) {
              alert(res.data)
              this.$router.push({
                path: '/'
              })
            } else {
              alert('Sign up failed. Try again.')
            }
          })
      },
      onLoginButton: function () {
        this.$router.push({
          path: '/login'
        })
      }
    },
    created () {
      window.scrollTo(0, 0)
    }
  }
</script>

<style scoped>
  .form-contents {
    max-width:500px;
    margin: 60px auto;
    padding: 0 24px;
  }

  .form-container {
    border-radius: 4px;
    box-shadow: 1px 1px 10px 1px #e4e4e4;
    /*border: 1px solid #484848;*/
    padding: 30px;
  }

  .form-container .email-container {

  }
  .form-container .email-container #input-email {
  }

  .form-container .button-container {
    text-align: center;
  }
  .form-container .button-container button {
    width: 100%;
  }
  .form-container .button-container #button-signup {
    margin-top: 80px;
    height: 60px;
  }

  .form-container .signup-container {
  }
  .form-container .signup-container #text-login {
    margin-top: 7px;
    float:left;
  }
  .form-container .signup-container #button-login {
    background: #FFF;
    color: #f2583d;
    border: 1px solid #f2583d;
    float:right;
    font-size:16px;
  }

  .form-container span {
    font-size: 15px;
  }
  .form-container input {
    font-size: 17px;
    height: 50px;
    margin-bottom:15px;
  }
</style>



