<template>
  <div class="page-container">

    <div class="form-contents">

    <auth-header></auth-header>

      <div class="form-container">
        <div class="row">

          <div class="col-md-12">
            <div class="email-container">
              <span>Email</span>
              <input v-model="email" :value="email" id="input-email" class="form-control" type="text" :placeholder="placeholder.email">
            </div>
          </div>

          <br>

          <div class="col-md-12">
            <div class="password-container">
              <span>Password</span>
              <input @keyup.enter="onLoginButton" v-model="password" :value="password" id="input-password" class="form-control" type="password" :placeholder="placeholder.password">
            </div>
          </div>

          <br>

          <div class="col-md-12">
            <div class="forgot-container">
              <a @click="onForgotPassword">{{msg.forgot}}</a>
            </div>
          </div>

          <div class="col-md-12">
            <div class="button-container">
              <button id="button-login" class="btn-lg btn-default" @click="onLoginButton" @keyup.enter="onLoginButton">
                Login
              </button>
            </div>
            <hr>
          </div>

          <div class="col-md-12">
            <div class="signup-container">
              <a id="text-signup" @click="onSignUp">Don't have an account?</a>
              <button class="btn btn-default" id="button-signup" @click="onSignUp">
                Sign up
              </button>
            </div>
          </div>

        </div>
      </div>

    </div>

    <footer-bar></footer-bar>
  </div>
</template>

<script>
  import AuthHeader from '../components/AuthHeader'
  import FooterBar from '../components/FooterBar'

  export default {
    metaInfo: {
      title: 'Factory Hunt - Log in'
    },
    components: {
      AuthHeader,
      FooterBar
    },
    data () {
      return {
        email: '',
        password: '',
        placeholder: {
          email: 'your@email.com',
          password: '*******'
        },
        msg: {
          forgot: 'forgot your password?'
        }
      }
    },
    methods: {
      onLoginButton: function () {
        const data = {
          email: this.email,
          password: this.password
        }
        this.$http.post('/api/auth/login', data)
          .then(res => {
            console.log(res.data)
            if (res.data.lead_id) {
              alert('Login success.')
              this.$router.push({
                path: '/'
              })
            } else {
              alert('Login failed.')
            }
          })
      },
      onForgotPassword: function () {
        alert('Comming soon.')
      },
      onSignUp: function () {
        this.$router.push({
          path: '/signup'
        })
      }
    },
    created () {
      window.scrollTo(0, 0)
    }
  }
</script>

<style scoped>
  .form-contents {
    max-width:500px;
    margin: 60px auto;
    padding: 0 24px;
  }

  .form-container {
    border-radius: 4px;
    box-shadow: 1px 1px 10px 1px #e4e4e4;
    /*border: 1px solid #484848;*/
    padding: 30px;
  }

  .form-container .email-container {
  }
  .form-container .email-container #input-email {
  }

  .form-container .forgot-container {
    margin-top: 120px;
    margin-bottom: 12px;
    font-size: 15px;
    text-align: right;
  }

  .form-container .button-container {
    text-align: center;
  }
  .form-container .button-container button {
    width: 100%;
  }
  .form-container .button-container #button-login {
    height: 60px;
  }

  .form-container .signup-container {
  }
  .form-container .signup-container #text-signup {
    margin-top: 7px;
    float:left;
  }
  .form-container .signup-container #button-signup {
    background: #FFF;
    color: #f2583d;
    border: 1px solid #f2583d;
    float:right;
    font-size:16px;
  }

  .form-container span {
    font-size: 15px;
  }
  .form-container input {
    font-size: 17px;
    height: 50px;
    margin-bottom:15px;
  }
</style>



